<template>
    <div class="background" ref="background"></div>
</template>

<script>
import * as THREE from 'three'
import { Water } from 'three/examples/jsm/objects/Water2'

export default {
  name: 'BackGround',
  data () {
    return {
    }
  },
  methods: {
    init () {
      const container = document.querySelector('.background')
      const fov = 30
      const aspect = container.clientWidth / container.clientHeight
      const near = 1
      const far = 500
      this.camera = new THREE.PerspectiveCamera(fov, aspect, near, far)
      this.camera.position.set(0, 20, 45)

      this.scene = new THREE.Scene()
      this.camera.lookAt(this.scene.position)

      const waterGeometry = new THREE.SphereGeometry(40, 60, 60)
      this.water = new Water(waterGeometry, {
        color: '#ffffff',
        scale: 2,
        textureWidth: 1024,
        textureHeight: 1024
      })
      this.water.position.x = 1

      const lineMaterial = new THREE.MeshBasicMaterial({})
      lineMaterial.color.setHSL(0, 100, 100)
      this.line = new THREE.LineSegments(new THREE.WireframeGeometry(waterGeometry), lineMaterial)

      this.group = new THREE.Group()
      this.group.add(this.water)
      this.group.add(this.line)

      this.scene.add(this.group)

      this.light = new THREE.DirectionalLight({ color: 0xfffff, intencity: 1 })
      this.camera.add(this.light)

      this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
      this.renderer.setPixelRatio(window.devicePixelRatio)
      this.renderer.setSize(container.clientWidth, container.clientHeight)
      container.appendChild(this.renderer.domElement)
      this.resize()
    },
    animate (time) {
      time *= 0.0001
      requestAnimationFrame(this.animate)
      this.line.rotation.x = time
      this.line.rotation.y = time
      this.renderer.render(this.scene, this.camera)
      this.resize()
    },
    resize () {
      const container = document.querySelector('.background')
      const width = container.clientWidth
      const height = container.clientHeight
      this.camera.aspect = width / height
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(width, height)
    }
  },
  mounted () {
    this.init()
    this.animate()
    this.resize()
  }
}
</script>
